<template>
  <echart :option="option" />
</template>
<script>
import { addMinutes } from "date-fns";
import Echart from "@/components/Echart.vue";

export default {
  mounted() {
    this.$unlock();
  },
  data() {
    return {
      option: {
        color: [
          "#60acfc",
          "#32d3eb",
          "#5bc49f",
          "#feb64d",
          "#ff7c7c",
          "#9287e7"
        ],
        legend: {
          data: ["沙河街道入城卡口", "沙河街道出城卡口", "天府三街入城卡口"],
          textStyle: {
            color: "#fff"
          },
          bottom: "10%",
          left: "4%"
        },
        textStyle: {
          color: "#fff"
        },
        grid: {
          top: "20%",
          bottom: "20%",
          left: "4%",
          right: "4%",
          containLabel: true
        },
        title: {
          text: "交通卡口车流统计",
          x: "center",
          top: "5%",
          textStyle: {
            color: "#fff"
          }
        },
        xAxis: {
          type: "time",
          axisTick: {
            show: false
          },
          splitLine: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "rgba(246,240,235,1)"
            }
          },
          data: ["01", "02", "03", "04", "05", "06", "07"]
        },
        yAxis: {
          name: "车次",
          axisTick: {
            show: false
          },
          splitLine: {
            lineStyle: {
              color: "#999"
            }
          },
          axisLine: {
            lineStyle: {
              color: "rgba(246,240,235,1)"
            }
          }
        },
        tooltip: {},
        series: [
          {
            name: "沙河街道入城卡口",
            type: "line",
            data: Array(10)
              .fill(1000)
              .map((item, index) => [
                addMinutes(Date.now(), index * 5),
                Math.random() * item + 1000
              ])
          },
          {
            name: "沙河街道出城卡口",
            type: "line",
            data: Array(10)
              .fill(1000)
              .map((item, index) => [
                addMinutes(Date.now(), index * 5),
                Math.random() * item + 1000
              ])
          },
          {
            name: "天府三街入城卡口",
            type: "line",
            data: Array(10)
              .fill(1000)
              .map((item, index) => [
                addMinutes(Date.now(), index * 5),
                Math.random() * item + 1000
              ])
          }
        ]
      }
    };
  },
  components: {
    Echart
  }
};
</script>